.NavbarLink {
    float: left;
    margin: .25em auto;
    font-size: 20px;
    font-weight: 400;
    text-decoration: none;
    color: #dea067;
}

a.NavbarLink {
    margin: none;
    text-decoration: none;
    color: #dea067 !important;
}

a.NavbarLink:hover {
    color: #000 !important;
    cursor: pointer !important;
}



