.AboutMe {
    text-align: center;
    display: flex;
    align-items: center;
    justify-content: center;
    height: 95vh;
}

.AboutMe-copy {
    text-align: center;
    display: flex;
    flex-wrap: wrap;
    margin: 1em auto;
    width: 100%;
    max-height: 100%;
    align-items: center;
    justify-content: center;
    text-shadow:
    0 2.8px 2.2px rgba(0, 0, 0, 0.034),
    0 6.7px 5.3px rgba(0, 0, 0, 0.048),
    0 12.5px 10px rgba(0, 0, 0, 0.06),
    0 22.3px 17.9px rgba(0, 0, 0, 0.072),
    0 41.8px 33.4px rgba(0, 0, 0, 0.086),
    0 100px 80px rgba(0, 0, 0, 0.12)
}



.AboutMe-header {
    margin-top: .25em;
    font-size: 48px;
    display: block;
}
