.Projects-container {
    color: inherit;
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    justify-content: center;
}

.Projects-header {
    display: block;
    font-size: 48px;
    margin: 1em 0;
    color: #212529;
    text-shadow: 
    0 2.8px 2.2px rgba(0, 0, 0, 0.034),
    0 6.7px 5.3px rgba(0, 0, 0, 0.048),
    0 12.5px 10px rgba(0, 0, 0, 0.06),
    0 22.3px 17.9px rgba(0, 0, 0, 0.072),
    0 41.8px 33.4px rgba(0, 0, 0, 0.086),
    0 100px 80px rgba(0, 0, 0, 0.12);
    
}

.Projects-text {
    display: block;
    width: 100%;
    text-align: center;
}


